@import 'colors.scss';
@import 'fonts.scss';
@import 'variables.scss';
@import 'breakpoints.scss';

@import 'card.scss';
@import 'code-view.scss';
@import 'front-page.scss';
@import 'heading.scss';
@import 'accordion-table.scss';
@import 'paragraph-highlight.scss';
@import 'sidebar.scss';
@import 'title-header.scss';
@import 'tabs.scss';

$bg: #f5f2f0;
$darkened-bg: darken($bg, 10%);

img {
    max-width: 100%;
}

body {
    font-size: 1.125rem;
}

.table {
    th,
    td {
        >.material-icons {
            float: none;
        }
    }
}

.jumbotron {
    /* TODO: Make .jumbotron @extend .slab. */
    font-family: Roboto, sans-serif;
    font-size: 21px;
    font-weight: 1;
    min-height: 1rem;
    padding: 3rem;
    margin-bottom: 1rem;
    border-radius: 4px;
    background-color: #ebe7e2;

    strong {
        font-weight: bold;
    }

    p:last-of-type {
        margin-bottom: 0;
    }
}

