#designguide .designguide-header .topbar-info a {
    margin-left: 5px;
    color: rgba(34, 34, 34, .75);
    fill: rgba(34, 34, 34, .75);
    border: none
}

#designguide .designguide-header .topbar-info a:after {
    content: none;
    display: inline;
}

#designguide .designguide-header .topbar-info a svg {
    overflow: visible;
    width: 2rem;
    height: 2rem;
    display: inline-block;
    vertical-align: top;
}

#designguide .designguide-header .topbar-info a:hover {
    color: rgba(0, 0, 0, .75);
    fill: rgba(0, 0, 0, .75)
}

.iterator {
    margin: 2rem 0;
}

.iterator a.btn {
    justify-content: center;
    width: 49%;
    min-width: 8rem;
}

.iterator a.btn[rel='prev']:before {
    content: '❮';
    position: absolute;
    left: 1rem;
}

.iterator a.btn[rel='next']:after {
    content: '❯';
    position: absolute;
    right: 1rem;
}

#designguide {
    .dg-sidebar {
        border-right: 1px solid $background-gray;

        @media screen and (max-width: $breakpoint-lg) {
            position: fixed;
        }

        .main-nav-ul {
            border-bottom: 1px solid $background-gray;

            .sidebar-secondary-nav {
                overflow-y: auto;
            }
        }
    }
}

.documentation {
    @media screen and (max-width: $breakpoint-lg) {
        overflow-y: auto;
    }

    > div {
        &.row {
            margin-right: 0;
        }
    }

    .doc-container {
        max-width: 100%;

        h2 {
            font-size: 1.75rem
        }
    }

    code[data-processed='true'].language-mermaid {
        background: none;
        border: none;
        padding: 0px
    }

    .highlighter-rouge {
        margin-bottom: 1rem;

        code {
            padding: 0;
            background-color: none;
            border: none;
            font-size: 1rem;
        }

        pre.highlight {
            background-color: #f5f2f0;
            margin: 0;
        }

        .rouge-table {
            position: relative;

            &,
            tbody,
            tr {
                display: block;
            }

            pre {
                font-family: 'Akkurat Mono', monospace;
            }

            .rouge-gutter {
                text-align: right;
                padding: 0 .7rem;
                position: absolute;
                left: 0;
                top: 0;
                background-color: #f5f2f0;
                border-right: 1px solid #e1d7d1;
            }

            .rouge-code {
                padding-left: 50px;
                display: block;
                overflow-x: auto;
            }
        }
    }

    .no-padding {
        padding: 0;
        padding-bottom: 5rem;
    }

    .normal-padding {
        padding: 1rem 1rem 5rem;
    }

    .max-width {
        max-width: $max-width;
    }

    .max-width-card-overview {
        max-width: 880px;
    }

    .row.card-list > div {
        display: flex;
        align-self: stretch;
    }

    .language-mermaid {
        $apricot: #fbf2ea;
        $pink: #efb7b6;
        $brand-info-light: #e8eff9;
        $brand-success-light: #f2f7eb;
        $darker-apricot: darken($apricot, 10%);
        $black: #222;
        $brown: #2f2424;

        svg {
            width: 100% !important;
            height: 100%;
        }

        rect.actor {
            fill: $apricot !important;
            stroke: $darker-apricot !important;
        }

        .activation0,
        .activation1,
        .activation2 {
            fill: $brand-success-light !important;
            stroke: darken($brand-success-light, 10%) !important;
        }

        text.actor {
            font-family: Arial, sans-serif !important;
            font-size: 1rem !important;
        }

        .actor-line {
            stroke: $apricot !important;
            stroke-width: 4 !important;
        }

        .messageLine0 {
            stroke-width: 2 !important;
            stroke-dasharray: "2 2";
            marker-end: "url(#arrowhead)";
            stroke: $black !important;
        }

        .messageLine1 {
            stroke-width: 1.5;
            stroke-dasharray: "2 2";
            stroke: $brown !important;
        }

        #arrowhead {
            fill: $brown !important;
        }

        .messageText {
            fill: $black !important;
            stroke: none;
            font-family: Arial, sans-serif !important;
            font-size: 1rem !important;
        }

        .labelBox {
            stroke: $pink !important;
            fill: $pink !important;
        }

        .labelText {
            fill: $black !important;
            stroke: none;
            font-family: Arial, sans-serif !important;
        }

        .loopText {
            fill: $black !important;
            stroke: none;
            font-family: Arial, sans-serif !important;
            font-size: 1rem !important;
        }

        .loopLine {
            stroke-width: 2;
            stroke-dasharray: "2 2";
            marker-end: "url(#arrowhead)";
            stroke: $pink !important;
        }

        .note {
            stroke: darken($brand-info-light, 20%) !important;
            fill: $brand-info-light !important;
        }

        .noteText {
            fill: $black !important;
            stroke: none;
            font-family: Arial, sans-serif !important;
            font-size: 1rem !important;
        }
    }

    .plantuml {
        text-align: center;
    }

    .table {
        .field-level {
            &::before {
                font-family: $font_mono;
                color: $yellow;
                content: '╰─╼';
            }

            code {
                border: 1px solid $yellow;
                border-radius: 8px;
            }
        }

        .field-level-0::before {
            content: '';
        }

        .field-level-2 {
            margin-left: 2rem;
        }

        .field-level-3 {
            margin-left: 4rem;
        }

        .field-level-4 {
            margin-left: 6rem;
        }

        .field-level-5 {
            margin-left: 8rem;
        }

        .field-level-6 {
            margin-left: 10rem;
        }
    }

    #github {
        opacity: .5;
        position: absolute;
        right: 4rem;
        top: 2rem;
        transition: opacity .3s;

        @media screen and (max-width: $breakpoint-lg) {
            top: 6rem;
        }

    }

    a#github:after {
        display: none;
    }

    a#github:hover {
        opacity: 1;
    }
}

#overlay {
    @media screen and (max-width: $breakpoint-lg) {
        position: fixed; /* Sit on top of the page content */
        display: none; /* Hidden by default */
        width: 100%; /* Full width (cover the whole page) */
        height: 100%; /* Full height (cover the whole page) */
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.5); /* Black background with opacity */
        z-index: 3; /* Specify a stack order in case you're using a different order for other elements */
    }
}

.search-box {
    position: absolute;
    right: 8rem;
    top: 1.5rem;
    z-index: 2;


    .search-input {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    #search-btn {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        height: 56px;
    }

    @media screen and (max-width: $breakpoint-lg) {
        position: fixed;
        right: 1rem;
        top: 1rem;
        z-index: 401;

        #tipue_search_input {
            display: none;
        }

        #search-btn {
            border-top-left-radius: 8px;
            border-top-left-radius: var(--border-radius);
            border-bottom-left-radius: 8px;
            border-bottom-left-radius: var(--border-radius);
        }
    }
}

.topbar-logo {
    @media screen and (max-width: $breakpoint-sm) {
        display: block;
        position: absolute;
    }
}
